.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
}

.modal__container {
  width: 670px;
